"use client";

import { useState, useEffect } from 'react';

// Define the vehicle data interface
interface Vehicle {
  vehicle_id: string;
  route_id: string;
  current_status: string;
  latitude: number;
  longitude: number;
  speed: number;
  bearing: number;
  timestamp: string;
  [key: string]: any; // Allow for additional properties
}

export default function VehicleData() {
  const [vehicleData, setVehicleData] = useState<Vehicle[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [routeId, setRouteId] = useState('26'); // Default route ID

  const fetchVehicleData = async () => {
    setLoading(true);
    setError(null);
    
    try {
      // Fetch from the Flask API directly
      const response = await fetch(`http://127.0.0.1:5000/api/vehicle-positions/${routeId}`);
      
      if (!response.ok) {
        throw new Error(`Failed to fetch data: ${response.status}`);
      }
      
      const data = await response.json();
      setVehicleData(data.vehicles || []);
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Unknown error occurred';
      setError(errorMessage);
      console.error('Error fetching vehicle data:', err);
    } finally {
      setLoading(false);
    }
  };

  // Fetch data on component mount
  useEffect(() => {
    fetchVehicleData();
    
    // Set up polling every 30 seconds
    const intervalId = setInterval(fetchVehicleData, 30000);
    
    // Clean up interval on component unmount
    return () => clearInterval(intervalId);
  }, [routeId]);

  return (
    <div className="p-6 max-w-6xl mx-auto">
      <h2 className="text-2xl font-bold mb-4">OCTA Vehicle Positions</h2>
      
      <div className="mb-4">
        <label className="block text-sm font-medium mb-1">Route ID:</label>
        <div className="flex">
          <input
            type="text"
            value={routeId}
            onChange={(e) => setRouteId(e.target.value)}
            className="border rounded px-3 py-2 mr-2 w-32"
            placeholder="Enter route ID"
          />
          <button 
            onClick={fetchVehicleData}
            disabled={loading}
            className="bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded"
          >
            {loading ? 'Loading...' : 'Refresh Data'}
          </button>
        </div>
      </div>
      
      {error && (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
          <p>Error: {error}</p>
        </div>
      )}
      
      {vehicleData.length > 0 ? (
        <div>
          <h3 className="text-xl font-semibold mb-2">Vehicles ({vehicleData.length})</h3>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {vehicleData.map((vehicle, index) => (
              <div key={index} className="border rounded p-4 bg-white shadow">
                <h4 className="font-bold mb-2">Vehicle ID: {vehicle.vehicle_id || 'N/A'}</h4>
                <div className="grid grid-cols-2 gap-2 text-sm">
                  <div>
                    <span className="font-medium">Route:</span> {vehicle.route_id || 'N/A'}
                  </div>
                  <div>
                    <span className="font-medium">Status:</span> {vehicle.current_status || 'N/A'}
                  </div>
                  <div>
                    <span className="font-medium">Latitude:</span> {vehicle.latitude || 'N/A'}
                  </div>
                  <div>
                    <span className="font-medium">Longitude:</span> {vehicle.longitude || 'N/A'}
                  </div>
                  <div>
                    <span className="font-medium">Speed:</span> {vehicle.speed ? `${vehicle.speed} mph` : 'N/A'}
                  </div>
                  <div>
                    <span className="font-medium">Bearing:</span> {vehicle.bearing ? `${vehicle.bearing}Â°` : 'N/A'}
                  </div>
                  <div>
                    <span className="font-medium">Updated:</span> {vehicle.timestamp || 'N/A'}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      ) : (
        <div className="text-center py-8">
          {loading ? (
            <p>Loading vehicle data...</p>
          ) : (
            <p>No vehicle data available. Try refreshing or checking a different route.</p>
          )}
        </div>
      )}
    </div>
  );
}